import React from 'react'; //jsx    js   React.createElement -   import React,     jsx'    React
import './App.css';
import {Route} from "react-router-dom";
import {withRouter} from "react-router-dom"
import HeaderContainer from './components/Header/HeaderContainer';
import Navbar from './components/Navbar/Navbar';
import ProfileContainer from './components/Profile/ProfileContainer';
import DialogsContainer from "./components/Dialogs/DialogsContainer";
import UsersContainer from "./components/Users/UsersContainer";
import LoginPage from "./components/LoginPage/LoginPage";
import {compose} from "redux";
import {connect} from "react-redux";
import {initializedAppThunkCreator} from "./redux/appReducer";
import Preloader from "./components/Common components/Preloader";
import NewsContainer from "./components/News/News";
import MusicContainer from "./components/Music/Music";
import SettingsContainer from "./components/Settings/Settings";
import FriendsContainer from "./components/Friends/Friends";

class App extends React.Component {
    componentDidMount() { //     ,      url
        this.props.initializedAppThunk() //  Thunk -
    }
    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }
        return (
            <div>
                <HeaderContainer/>
                <div className='app-wrapper'>
                    <Navbar/>
                    <div className='app-wrapper-content'>
                        {/* Route  ,     */}
                        {/*   render   ,   */}
                        <Route
                            path='/profile/:userId?' // ,     ,    ( ?)
                            render={() => <ProfileContainer/>}/>

                        {/* Route  ,     */}
                        {/*   render   ,   */}
                        <Route path='/dialogs'
                               render={() => <DialogsContainer/>}/>
                        {/*<Route path='/news' component={News}/>
                    <Route path='/music' component={Music}/>
                    <Route path='/settings' component={Settings}/>*/}

                        {/* Route  ,     */}
                        {/*   render   ,   */}
                        <Route path='/users'
                               render={() => <UsersContainer/>}/>

                        {/* Route  ,     */}
                        {/*   render   ,   */}
                        <Route path='/login'
                               render={() => <LoginPage/>}/>
                        <Route path='/news'
                               render={() => <NewsContainer/>}/>
                        <Route path='/music'
                               render={() => <MusicContainer/>}/>
                        <Route path='/settings'
                               render={() => <SettingsContainer/>}/>
                        <Route path='/friends'
                               render={() => <FriendsContainer/>}/>
                    </div>
                </div>
            </div>
        );
    }
}

//   f connect   store.getState()
let mapStateToProps = (state) => {//  state  props   . .
    return { //f,   
        initialized: state.app.initialized,
    }
}

export default compose( //3.) connect  DialogsContainer
    withRouter, // 2.)  App  hoc withRouter
    // 1.)  hoc  connect   STATE   DISPATCH
    connect(mapStateToProps,
        { //  mapDispatchToProps = (dispatch) => { return{} }
            initializedAppThunk: initializedAppThunkCreator, //  f =>     creator =>
            //   thunk =>   
        }),
)(App)