import * as axios from "axios"

// axios    UsersContainer ,  DAL (Data Access Layer)
const instance = axios.create({ //  axios
    baseURL: `https://social-network.samuraijs.com/api/1.0/`,
    withCredentials: true,
    headers: {
        "API-KEY": "04cbf382-5051-418e-bc14-aa121ee09f50",
    },
})

export const usersAPI = { //     endpoint,   
    getUsers(currentPage = 1, displayUsersNumber = 10) {
        return instance.get(`users?page=${currentPage}&count=${displayUsersNumber}`,)
            //  =page=  ,   =count= 
            .then(response => { // 
                return response.data; //  ,    UsersContainer
            })
    },
    unfollow(userId) {
        return instance.delete(`follow/${userId}`,)
            .then(response => { // 
                return response.data; //  ,    UsersContainer
            })
    },
    follow(userId) {
        return instance.post(`follow/${userId}`,)
            .then(response => { // 
                return response.data; //  ,    UsersContainer
            })
    }
}

export const profileAPI = { //     endpoint,   
    getProfile(userId) { //    get
        return instance.get(`profile/` + userId)
            .then(response => { // 
                return response.data
            })
    },
    getStatus(userId) {
        return instance.get('profile/status/' + userId)
            .then(response => {
                return response.data
            })
    },
    updateStatus(userStatus) {
        return instance.put('profile/status', {
            status: userStatus
        })
            .then(response => {
                return response.data
            })
    },
}

export const authAPI = { //,       
    me() { //    get
        return instance.get(`auth/me`)
            .then(response => { // 
                return response.data;
            })
    },
    login(email, password, rememberMe = false) { //    post
        return instance.post('auth/login', {
            email: email,
            password: password,
            rememberMe: rememberMe,
        })
            .then(response => { // 
                return response.data;
            })
    },
    logout() { //    delete
        return instance.delete('auth/login') //      
            .then(response => { // 
                return response.data;
            })
    },
}



