import {authAPI} from "./../api/api";
import {stopSubmit} from "redux-form";

let SET_USER_AUTH_DATA = "authReducer/SET_USER_AUTH_DATA" // ,     
let TOGGLE_IS_FETCHING = "authReducer/TOGGLE_IS_FETCHING"

let initialState = { //  state    reducer,     state  
    userId: null,
    login: null,
    email: null,
    isFetching: false, // ?
    isAuth: false, //?
}

const authReducer = (state = initialState, action) => {
    switch (action.type) { //   action

        case SET_USER_AUTH_DATA:
            return { //   
                ...state,
                ...action.payload, //     state
            } //   state

        case TOGGLE_IS_FETCHING:
            return { //   
                ...state,
                isFetching: action.isFetching, //  initialState   
            }

        default:
            return state
    }
}

export const setUserAuthDataAC = (userId, email, login, isAuth) => ({
    type: SET_USER_AUTH_DATA, payload:
        {userId, email, login, isAuth}
});

export const toggleIsFetchingAC = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching: isFetching})// action.type

export const getAuthUserThunkCreator = () => { // f ,    getAuthUserThunk,
    //      
    return async (dispatch) => { // f,      action creators

        dispatch(toggleIsFetchingAC(true)) //  preloader   

        let data = await authAPI.me() // get    api.js
        if (data.resultCode === 0) { //  
            let {userId, login, email} = data.data;
            dispatch(setUserAuthDataAC(userId, email, login, true)) //    reducer

            dispatch(toggleIsFetchingAC(false)) //  preloader   
        }
    }
}

export const loginThunkCreator = (email, password, rememberMe) => { // f ,    loginThunk,
    //      
    return async (dispatch) => { // f,      action creators
        let data = await authAPI.login(email, password, rememberMe) // post    api.js
        if (data.resultCode === 0) { //  
            dispatch(getAuthUserThunkCreator()) //    getAuthUserThunk
        } else {
            let messageError = (data.messages.length > 0) ? data.messages[0] : '-  :(' //
            dispatch(stopSubmit("login", {_error: messageError})) //  
            //      ,     
        }
    }
}

export const logoutThunkCreator = () => { // f ,    logoutThunk,
    //      
    return async (dispatch) => { // f,      action creators
        let data = await authAPI.logout() // delete    api.js
        if (data.resultCode === 0) { //  
            //  ,    ,     
            dispatch(setUserAuthDataAC(null, null, null, false))
        }
    }
}

export default authReducer