import {usersAPI} from './../api/api';

let FOLLOW = "usersPageReducer/FOLLOW" // ,     
let UNFOLLOW = "usersPageReducer/UNFOLLOW"
let SET_USERS = "usersPageReducer/SET_USERS"
let SET_CURRENT_PAGE = "usersPageReducer/SET_CURRENT_PAGE"
let SET_TOTAL_COUNT_USERS = "usersPageReducer/SET_TOTAL_COUNT_USERS"
let TOGGLE_IS_FETCHING = "usersPageReducer/TOGGLE_IS_FETCHING"
let TOGGLE_IS_FOLLOWING_PROGRESS = "usersPageReducer/TOGGLE_IS_FOLLOWING_PROGRESS"


let initialState = { //  state    reducer,     state  
    usersData: [], /*   MyPosts,     */
    totalUsersCount: 30,
    displayUsersNumber: 12,
    currentPage: 1,
    isFetching: false, // ?
    followingInProgress: [], //  
}

const usersPageReducer = (state = initialState, action) => {
    switch (action.type) { //   action

        case FOLLOW:
            return { //   
                ...state,
                usersData: state.usersData.map((element, id) => { //  -   map
                    if (element.id === action.userId) { //if id   = id   
                        return {...element, followed: true} //    :true
                    }
                    return element //      
                }),
            } //   state,     

        case UNFOLLOW:
            return { //   
                ...state,
                usersData: state.usersData.map((element, id) => { //  -   map
                    if (element.id === action.userId) { //if id   = id   
                        return {...element, followed: false} //    :true
                    }
                    return element //      
                }),
            } //   state,     

        case SET_USERS:
            return {
                ...state,
                usersData: action.users //     action
                //   onClick    
            }

        case SET_CURRENT_PAGE:
            return { //   
                ...state,
                currentPage: action.currentPage, //  initialState   
            }
        case SET_TOTAL_COUNT_USERS:
            return { //   
                ...state,
                totalUsersCount: action.totalCount, //  initialState   ,   
            }
        case TOGGLE_IS_FETCHING:
            return { //   
                ...state,
                isFetching: action.isFetching, //  initialState   
            }
        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return { //   
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId] // ,     userId
                    : state.followingInProgress.filter(id => id != action.userId) //     
            }
        default:
            return state
    }
}

export const followAC = (userId) => ({type: FOLLOW, userId})//,  action.type

export const unfollowAC = (userId) => ({type: UNFOLLOW, userId})//,  action.type

export const setUsersAC = (users) => ({type: SET_USERS, users})//,  action.type

export const setCurrentPageAC = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage: currentPage})//,  action.type

export const setTotalCountUsersAC = (totalCount) => ({type: SET_TOTAL_COUNT_USERS, totalCount: totalCount})//,  action.type

export const toggleIsFetchingAC = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching: isFetching})//,  action.type

export const toggleIsFollowingProgressAC = (isFetching, userId) => ({
    type: TOGGLE_IS_FOLLOWING_PROGRESS,
    isFetching,
    userId
})//,  action.type

export const getUsersThunkCreator = (currentPage, displayUsersNumber) => { // f ,    getUsersThunk,
    //      
    return async (dispatch) => { // f,      action creators

        dispatch(toggleIsFetchingAC(true)) //  preloader   

        let data = await usersAPI.getUsers(currentPage, displayUsersNumber)// get    api.js
        dispatch(setUsersAC(data.items)) //  items,     
        dispatch(setTotalCountUsersAC(data.totalCount)) //   
        dispatch(toggleIsFetchingAC(false)) //  preloader   
    }
}

export const unfollowThunkCreator = (userId) => { // f ,    unfollowThunk,
    //      
    return async (dispatch) => { // f,      action creators

        dispatch(toggleIsFollowingProgressAC(true, userId)) // ? YES

        let data = await usersAPI.unfollow(userId) // delete    api.js,    
        if (data.resultCode == 0) { //  ,   
            dispatch(unfollowAC(userId))
        }

        dispatch(toggleIsFollowingProgressAC(false, userId))// ? NO
    }
}

export const followThunkCreator = (userId) => { // f ,    followThunk,
    //      
    return async (dispatch) => { // f,      action creators

        dispatch(toggleIsFollowingProgressAC(true, userId)) // ? YES

        let data = await usersAPI.follow(userId) // post    api.js,    
        if (data.resultCode == 0) { //  ,   
            dispatch(followAC(userId))
        }

        dispatch(toggleIsFollowingProgressAC(false, userId))// ? NO
    }
}

export default usersPageReducer