import {createSelector} from "reselect"

export const getUsersDataSelector = (state) => { //    ,     
    return state.usersPage.usersData
}

// getUsersDataSuperSelector  state 
export const getUsersDataSuperSelector = createSelector(getUsersDataSelector, //    usersData
    (usersData) => { //usersData  ,   SuperSelector     
    return usersData //     ,           
})

export const getTotalUsersCountSelector = (state) => {
    return state.usersPage.totalUsersCount
}

export const getDisplayUsersNumberSelector = (state) => {
    return state.usersPage.displayUsersNumber
}

export const getCurrentPageSelector = (state) => {
    return state.usersPage.currentPage
}

export const getIsFetchingSelector = (state) => {
    return state.usersPage.isFetching
}

export const getFollowingInProgressSelector = (state) => {
    return state.usersPage.followingInProgress
}

