import React from 'react';
import Dialogs from "./Dialogs";
import {sendNewMessageCreator} from "./../../redux/dialogsPageReducer"
// ,  action  
import {connect} from "react-redux";
import {compose} from "redux";

//   f connect   store.getState()
let mapStateToProps = (state) => {//  state  props   . .
    return { //f,   
        dialogsPage: state.dialogsPage,
    }
}

//   f connect   store.dispatch.bind(store)
let mapDispatchToProps = (dispatch) => { //     
    return { //f,   
        sendNewMessage: (textMessage) => {
            dispatch( sendNewMessageCreator(textMessage) )
        }
    }
}

const DialogsContainer = compose(
    connect(mapStateToProps, mapDispatchToProps), // hoc  connect   STATE   DISPATCH
)(Dialogs)

export default DialogsContainer;