import React from 'react'
import MyPosts from './MyPosts'
import {addNewPostActionCreator} from "./../../../redux/profilePageReducer";
// ,  action  
import {connect} from "react-redux";

//   f connect   store.getState()
let mapStateToProps = (state) => {//  state  props   . .
    return{ //f,   
        postsData: state.profilePage.postsData,
    }
}

//   f connect   store.dispatch.bind(store)
let mapDispatchToProps = (dispatch) => { //     
    return{ //f,   
        addNewPost: (newPostText) => {
            dispatch( addNewPostActionCreator(newPostText) )//    store   
        },
    }
}

const MyPostsContainer = connect(mapStateToProps,mapDispatchToProps)(MyPosts) // Dialogs   store   . .


export default MyPostsContainer