import {profileAPI} from "./../api/api";

let ADD_NEW_POST = "profilePageReducer/ADD_NEW_POST" // ,     
let DELETE_POST = "profilePageReducer/DELETE_POST" // ,     

let SET_USER_PROFILE = "profilePageReducer/SET_USER_PROFILE"
let SET_USER_STATUS = "profilePageReducer/SET_USER_STATUS"

let initialState = { //  state    reducer,     state  
    postsData: [ /*   MyPosts,     */
        {id: 1, message: 'Hi, how are you doing??', likesCount: '10'},
        {id: 2, message: 'Yana, you are fantastic person!', likesCount: '23'},
    ],
    profile: null,
    userStatus: "",
}

const profilePageReducer = (state = initialState, action) => {
    if (action.type === ADD_NEW_POST) { //   action
        let newPost = { //  
            id: 3,
            /*id: this._state.dialogPage.messages[this._state.dialogPage.messages.length - 1].id + 1,*/
            message: action.newPostText,
            likesCount: '0',
        }
        return { //   
            ...state,
            postsData: [...state.postsData, newPost/* push*/], //   state        state  onClick
        } //   state,     
    } else if (action.type === DELETE_POST) { //   action
        return { //   
            ...state,
            postsData: state.postsData.filter(posts => posts.id != action.id),
        } //for test
    } else if (action.type === SET_USER_PROFILE) { //   action
        return { //   
            ...state,
            profile: action.profile //   state
        }
    } else if (action.type === SET_USER_STATUS) {
        return {
            ...state,
            userStatus: action.userStatus
        }
    }
    return state
}

export const addNewPostActionCreator = (newPostText) => ({type: ADD_NEW_POST, newPostText: newPostText})//,  action.type  f addNewPost
export const deletePostActionCreator = (id) => ({type: DELETE_POST, id}) //for test

export const setUserProfileAC = (profile) => {  //,  action
    return { //    action  dispatch
        type: SET_USER_PROFILE,
        profile: profile,
    }
}

export const getUserProfileThunkCreator = (userId) => { // f ,    getUserProfileThunk,
    //      
    return async (dispatch) => { // f,      action creators
        let data = await profileAPI.getProfile(userId) // get    api.js
        dispatch(setUserProfileAC(data)) //  profile  state
    }
}

export const setUserStatusAC = (userStatus) => {  //,  action
    return { //    action  dispatch
        type: SET_USER_STATUS,
        userStatus: userStatus,
    }
}

export const getUserStatusThunkCreator = (userId) => {
    return async (dispatch) => { // f,      action creators
        let data = await profileAPI.getStatus(userId) // get    api.js
        dispatch(setUserStatusAC(data)) //  status  api  state
    }
}

export const updateUserStatusThunkCreator = (userStatus) => {
    return async (dispatch) => { // f,      action creators
        let data = await profileAPI.updateStatus(userStatus) // put    api.js    api  userStatus
        if (data.resultCode === 0) { //   
            dispatch(setUserStatusAC(userStatus)) //  userStatus  state
        }
    }
}

export default profilePageReducer