import React from 'react' //jsx    js   React.createElement -   import React,
//     jsx'    React
import styles from './LoginPage.module.css'
import {Field, reduxForm} from "redux-form";
import {required} from "./../../utils/validators/validators";
import {Input} from "./../Common components/FormsControl/FormsControl";
import {connect} from "react-redux";
import {loginThunkCreator, logoutThunkCreator} from "../../redux/authReducer";
import {Redirect} from "react-router-dom";

const LoginForm = (props) => {
    return (
        <div className={styles.loginForm}>
            <form onSubmit={props.handleSubmit}> {/*2.)        */}
                <div>
                    <Field component={Input} validate={[required]}
                           name={"email"} placeholder={"Email"}/> {/*Field     Redux form*/}
                    {/*  name (  ,      login,      form)*/}
                </div>
                <div>
                    <Field component={Input} validate={[required]} type={"password"}
                           name={"password"} placeholder={"Password"}/>
                </div>
                <div>
                    <Field component={Input} validate={[required]}
                           name={"rememberMe"} type={"checkbox"}/>
                    <span>remember me</span>
                </div>
                {props.error && <div className={styles.formSummaryError}>
                    {props.error}
                </div>} {/*    */}
                <div className={styles.sendButton}>
                    <button>Send yours data</button>
                </div>
            </form>
        </div>
    )
}

/*1.1.)     handleSubmit,    formData*/
const LoginReduxForm =  reduxForm({ // hoc ReduxForm   LoginForm
    form: 'login' //     ,  reduxForm  
})(LoginForm)

const LoginPage = (props) => {
    const onSubmit = (formData) => { //1.)  f,       
        props.loginThunk(formData.email, formData.password, formData.rememberMe) //     
    }
    if (props.isAuth) { // ,    
        return <Redirect to={"/profile"} />
    }
    return (
        <div className={styles.loginPage}>
            <h1>LOGIN</h1>
            <LoginReduxForm onSubmit={onSubmit}/> {/*1.)  f,       */}
        </div>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps,
    { //  mapDispatchToProps = (dispatch) => { return{} }
        loginThunk: loginThunkCreator, //  f =>     creator =>
        //   thunk =>   
        logoutThunk: logoutThunkCreator,
    })(LoginPage)