import {getAuthUserThunkCreator} from "./authReducer";

let INITIALIZED_SUCCESSED = "INITIALIZED_SUCCESSED" // ,     

let initialState = { //  state    reducer,     state  
    initialized: false, //. ;  -,    
}

const appReducer = (state = initialState, action) => {
    switch (action.type) { //   action

        case INITIALIZED_SUCCESSED:
            return { //   
                ...state,
                initialized: true,
            }

        default:
            return state
    }
}

export const initializedSuccessedAC = () => ({type: INITIALIZED_SUCCESSED});

export const initializedAppThunkCreator = () => { // f ,    initializedThunk,
    //      
    return (dispatch) => { //f,      action creators
        let promise = dispatch(getAuthUserThunkCreator()) //        authReducer.js
        Promise.all([promise]) //    
        //     
        .then( () => { //  ,
            //(  resolve   ,  . .. ( ) -  "")
            dispatch(initializedSuccessedAC()) //,  
        })
    }
}

export default appReducer