import React, {useState, useEffect} from "react";
import styles from './ProfileStatus.module.css';

const ProfileStatusWithHooks = (props) => {
    let editModeState = useState(false) // useState     
    let editMode = editModeState[0] //  ,   -  
    let setEditMode = editModeState[1] //f,     
    // let [editMode, setEditMode] = useState(false)      

    let statusState = useState(props.userStatus) // useState     
    let status = statusState[0] //  ,   -  
    let setStatus = statusState[1] //f,     
    // let [status, setStatus] = useState(props.userStatus)      

    useEffect(  () => { //         
        setStatus(props.userStatus)
    }, [props.userStatus]) //    userStatus

    const activateEditMode = () => { //   ,    ,    state
        setEditMode(true)
    }

    const deactivateEditMode = () => { //   ,    ,    state
        setEditMode(false)
        props.updateUserStatusThunk(status) // userStatus  DAL  =>   
    }

    const onStatusChange = (e) => { //input    f  (event)
        setStatus(e.currentTarget.value) //target  input
    }

    const noStatus = () => {
        return (
            <span className={styles.noStatus}>no status</span>
        )
    }

    return (
        <div className={styles.profileStatus}>
            {editMode &&
            <div>
                <input onChange={onStatusChange} autoFocus={true} onBlur={deactivateEditMode}
                       value={status}/>
            </div>
            }
            {!editMode &&
            <div onDoubleClick={activateEditMode} className={styles.userText}>
                Status: {props.userStatus != null ? props.userStatus : noStatus()}
            </div>
            }
        </div>
    )
}

export default ProfileStatusWithHooks;