import React from 'react'
import {connect} from "react-redux"
import Users from './Users'
import Preloader from './../Common components/Preloader'
import {
    setCurrentPageAC,
    getUsersThunkCreator,
    followThunkCreator,
    unfollowThunkCreator
} from "./../../redux/usersPageReducer"
import {compose} from "redux";
import {
    getUsersDataSuperSelector,
    getCurrentPageSelector,
    getDisplayUsersNumberSelector,
    getFollowingInProgressSelector,
    getIsFetchingSelector,
    getTotalUsersCountSelector,
} from "./../../redux/usersPageSelectors"

// ,  action  

class UsersContainerClass extends React.Component {

    componentDidMount() { //     ,      url

        this.props.getUsersThunk(this.props.currentPage, this.props.displayUsersNumber) //    
    }

    onPageChanged = (currentPage) => { //     OnClick

        this.props.setCurrentPage(currentPage) //   

        this.props.getUsersThunk(currentPage, this.props.displayUsersNumber) //    
    }

    render() { //  props      
        return <> {/*      ,  fake */}
            {
                this.props.isFetching ? <Preloader/> : null // 
            }
            <Users totalUsersCount={this.props.totalUsersCount}
                   displayUsersNumber={this.props.displayUsersNumber}
                   currentPage={this.props.currentPage}
                   usersData={this.props.usersData}
                   followingInProgress={this.props.followingInProgress}

                   unfollowThunk={this.props.unfollowThunk}
                   followThunk={this.props.followThunk}
                   onPageChanged={this.onPageChanged}
            />
        </>
    }
}

//   f connect   store.getState()
let mapStateToProps = (state) => {//  state  props   . .
    return { //f,   
        usersData: getUsersDataSuperSelector(state),
        totalUsersCount: getTotalUsersCountSelector(state),
        displayUsersNumber: getDisplayUsersNumberSelector(state),
        currentPage: getCurrentPageSelector(state),
        isFetching: getIsFetchingSelector(state),
        followingInProgress: getFollowingInProgressSelector(state),
    }
}


const UsersContainer = compose( //3.) connect  UsersContainer
    connect(mapStateToProps, // 2.)  hoc  connect   STATE   DISPATCH
    { //  mapDispatchToProps = (dispatch) => { return{} }
        setCurrentPage: setCurrentPageAC, //     f,     
        getUsersThunk: getUsersThunkCreator, //  f =>     creator =>
        //   thunk =>   
        unfollowThunk: unfollowThunkCreator, //  f =>     creator =>
        //   thunk =>   
        followThunk: followThunkCreator, //  f =>     creator =>
        //   thunk =>   
    }),
    /*withAuthRedirect, // 1.)  UsersContainerClass  hoc*/
)
(UsersContainerClass)

export default UsersContainer
