import React from 'react'
import s from './MyPosts.module.css'
import Post from './Post/Post'
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {Textarea} from "./../../Common components/FormsControl/FormsControl";

const maxLength10 = maxLengthCreator(20)

const AddPostForm = (props) => {
    return (
        <div className={s.postForm}>
            <form
                onSubmit={props.handleSubmit}> {/*2.)        */}
                <div>
                    {/*Field     Redux form*/}
                    <Field className={s.textArea} component={Textarea} name={"newPostText"} placeholder={"       "}
                           validate={[required, maxLength10]}/>
                </div>
                <button>send post to user page</button>
            </form>
        </div>
    )
}

/*1.1.)     handleSubmit,    formData*/
const AddPostFormRedux = reduxForm({ //  ReduxForm   DialogsForm
    form: 'postAddTextForm' //     ,  reduxForm  
})(AddPostForm)

const MyPosts = (props) => {
    let postElements = props.postsData
        .map((element, i) => <Post key={i} id={element.id} message={element.message}
                                   likesCount={element.likesCount}/>)

    let onAddNewPost = (values) => {
        props.addNewPost(values.newPostText) //newPostText  name  Field
    }

    return (
        <div className={s.myPosts}>
            <div className={s.addPost}>
                <AddPostFormRedux onSubmit={onAddNewPost}/> {/*1.)  f,       */}
            </div>
            <div className={s.postedPosts}>
                <h2>All your sent posts recently:</h2>
                <div className={s.posts}>
                    {postElements}
                </div>
            </div>
        </div>
    )
}

export default MyPosts