import React from "react";
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";

let mapStateToPropsForRedirect = (state) => ({ //  RedirectComponent
    isAuth: state.auth.isAuth, //  AuthReducer
})

export const withAuthRedirect = (Component) => { // f    

    class RedirectComponent extends React.Component{ //     
        render() {
            if(!this.props.isAuth) return <Redirect to={"/login"}/> //  true ( ) => redirect   

            return <Component {...this.props}/> //      
        }
    }

    let connectedRedirectComponent = connect(mapStateToPropsForRedirect)(RedirectComponent) //    

    return connectedRedirectComponent //   ,   2  
}