import React from 'react'
import s from './Profile.module.css'
import Profile from './Profile'
import {connect} from "react-redux";
import {
    getUserProfileThunkCreator,
    getUserStatusThunkCreator,
    updateUserStatusThunkCreator
} from "./../../redux/profilePageReducer";
import {Redirect, withRouter} from "react-router-dom";
import {compose} from "redux";
import {getUsersDataSuperSelector} from "./../../redux/usersPageSelectors";

class ProfileContainerClass extends React.Component {

    refreshProfile() {
        let userId = this.props.match.params.userId //         path ProfileContainer
        if (!userId) {
            userId = this.props.authorizedUserId
            userId = 11410
            if (!userId) { //  authorizedUserId = 0 or undefined
                this.props.history.push("/login") //   */
                return <Redirect to={"/login"}/>
            }
        }
        this.props.getUserProfileThunk(userId) //    id  
        this.props.getUserStatusThunk(userId) //    id  
    }

    componentDidMount() { //     ,      url
        this.refreshProfile()
    }

    componentDidUpdate(prevProps, prevState, snapshot) { //     
        if (this.props.match.params.userId != prevProps.match.params.userId) {
            this.refreshProfile()
        }
    }

    render() {
        return (
            <div className={s.content}>
                <Profile {...this.props} /*  = */
                         profile={this.props.profile}
                         userStatus={this.props.userStatus}
                         userId={this.props.match.params.userId}
                         usersData={this.props.usersData}
                         updateUserStatusThunk={this.props.updateUserStatusThunk}/>
            </div>
        )
    }
}

//   f connect   store.getState()
let mapStateToProps = (state) => {//  state  props   . .
    return { //f,   
        profile: state.profilePage.profile,
        userStatus: state.profilePage.userStatus,
        isAuth: state.auth.isAuth,
        authorizedUserId: state.auth.userId,
        usersData: getUsersDataSuperSelector(state),
    }
}

const ProfileContainer = compose(
    connect(mapStateToProps, { //3.)  f withRouter  connect   STATE   DISPATCH
        getUserProfileThunk: getUserProfileThunkCreator,
        getUserStatusThunk: getUserStatusThunkCreator,
        updateUserStatusThunk: updateUserStatusThunkCreator,
        //  f =>     creator =>   thunk =>   
    }),
    withRouter, // 2.)  hoc  f withRouter,   url 
    // withAuthRedirect,  1.)  ProfileContainerClass  hoc
)
(ProfileContainerClass)

export default ProfileContainer